* {
  padding: 0px;
  margin: 0px;

  box-sizing: border-box;
  font-family: sans-serif;
}

body {
  background-color: rgb(48, 52, 59);
  color: rgb(255, 255, 255);
}

.container {
  width: 100%;
}

.chat {
  height: 85vh;
  padding-top: 30px;
}

.message {
  font-size: 1.125rem;
  padding: 5px 10px;
  margin: 10px;
  max-width: 75%;
  width: fit-content;
  word-wrap:break-word;
  color: #e6e6e6;
}

.sent {
  border-radius: 10px;
  border-top-right-radius: 0px;
  background-color: rgb(80, 146, 90);
  margin-left: auto; 
  margin-right: 15px;
}

.received {
  border-radius: 10px;
  border-top-left-radius: 0px;
  background-color: rgb(111, 111, 111);
}

.controls {
  width: 100%;
  position: fixed;
  bottom: 0;
}

.txt {
  width: 80%;
  padding: 5px;
  background-color: rgb(38, 43, 48);
  color: #fff;
}

.snd {
  width: 20%;
  padding: 5px;
  background-color: rgb(38, 43, 48);
  color: #fff;
}

.header {
  position: fixed;
  top: 0;
  background-color: #2e2e2e;
  width: 100%;
  /* height: 20px; */
  display: flex;
  justify-content: space-between;
  padding: 5px;
}

.centered {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.txtfield {
  padding: 10px;
  background-color: #2e2e2e;
  color: #fff;
}

.btn {
  padding: 10px;
  background-color: rgb(80, 146, 90);
  border-radius: 10px;

  color: #e6e6e6;
}